import type { AnalysisResult } from "@/lib/api"
import { Activity, ArrowLeft, Download, ShieldCheck } from "lucide-react"
import { useState } from "react"

interface DiagnosticDetailViewProps {
  onBack: () => void
  data: AnalysisResult | null
}

export function DiagnosticDetailView({ onBack, data }: DiagnosticDetailViewProps) {
  const [activeLayer, setActiveLayer] = useState<'MASK' | 'HEATMAP'>('HEATMAP')

  if (!data) return <div className="text-white p-10">No Analysis Data Available</div>

  // Helper to ensure Base64 string works in <img> tag
  const getImageSrc = (b64: string) => {
    if (!b64) return ""
    return b64.startsWith("data:") ? b64 : `data:image/png;base64,${b64}`
  }

  return (
    <div className="space-y-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
      
      {/* HEADER */}
      <div className="flex items-center justify-between">
        <button
          onClick={onBack}
          className="flex items-center gap-2 text-slate-400 hover:text-white transition-colors"
        >
          <ArrowLeft size={20} /> Back to Upload
        </button>
        <div className="flex items-center gap-2 px-3 py-1 bg-green-500/10 border border-green-500/20 rounded-full text-xs font-medium text-green-400">
          <ShieldCheck size={14} /> Verified by Midnight
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        
        {/* LEFT: IMAGE VIEWER */}
        <div className="space-y-4">
          <div className="bg-slate-900/50 border border-white/10 rounded-3xl p-2 relative overflow-hidden group h-[500px] flex items-center justify-center">
            
            {/* The Dynamic Image from Backend */}
            <img
              src={activeLayer === 'MASK' ? getImageSrc(data.segmentation_mask) : getImageSrc(data.grad_cam_heatmap)}
              alt="Medical Analysis"
              className="max-h-full max-w-full object-contain rounded-xl"
            />

            {/* Layer Controls */}
            <div className="absolute bottom-6 left-1/2 -translate-x-1/2 bg-black/80 backdrop-blur-md border border-white/10 rounded-full p-1.5 flex gap-2">
               <button
                 onClick={() => setActiveLayer('MASK')}
                 className={`px-6 py-2 rounded-full text-xs font-bold transition-all ${activeLayer === 'MASK' ? 'bg-cyan-500 text-black' : 'text-slate-400 hover:text-white'}`}
               >
                 Segmentation Mask
               </button>
               <button
                 onClick={() => setActiveLayer('HEATMAP')}
                 className={`px-6 py-2 rounded-full text-xs font-bold transition-all ${activeLayer === 'HEATMAP' ? 'bg-purple-500 text-white' : 'text-slate-400 hover:text-white'}`}
               >
                 Grad-CAM Heatmap
               </button>
            </div>
          </div>
        </div>

        {/* RIGHT: DATA PANEL */}
        <div className="space-y-6">
          
          {/* Diagnosis Card */}
          <div className="bg-slate-800/50 border border-white/5 p-6 rounded-3xl">
            <h3 className="text-slate-400 text-xs font-bold uppercase tracking-wider mb-2">Primary Diagnosis</h3>
            <div className="text-3xl font-bold text-white mb-4">{data.diagnosis}</div>

            <div className="space-y-2">
              <div className="flex justify-between text-sm">
                <span className="text-slate-400">Confidence Score</span>
                <span className="text-cyan-400 font-mono">{(data.confidence * 100).toFixed(1)}%</span>
              </div>
              <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
                <div
                  className="h-full bg-cyan-500 rounded-full transition-all duration-1000"
                  style={{ width: `${data.confidence * 100}%` }}
                />
              </div>
            </div>
          </div>

          {/* Privacy Proof Card */}
          <div className="bg-slate-800/50 border border-white/5 p-6 rounded-3xl">
            <h3 className="text-slate-400 text-xs font-bold uppercase tracking-wider mb-4">Midnight Privacy Proof</h3>
            <div className="bg-black/50 rounded-xl p-4 font-mono text-xs text-green-400 break-all border border-green-500/20">
              {data.midnight_proof_hash}
            </div>
            <div className="mt-4 flex gap-4 text-xs text-slate-500">
               <div className="flex items-center gap-2">
                 <ShieldCheck size={14} className="text-green-500"/> ZK Circuit Valid
               </div>
               <div className="flex items-center gap-2">
                 <Activity size={14} className="text-green-500"/> Masumi Audit Logged
               </div>
            </div>
          </div>

          {/* SHAP Values (Explainability) */}
          {data.shap_values && (
            <div className="bg-slate-800/50 border border-white/5 p-6 rounded-3xl">
               <h3 className="text-slate-400 text-xs font-bold uppercase tracking-wider mb-4">Explainability Factors (SHAP)</h3>
               <div className="space-y-3">
                 {Object.entries(data.shap_values).map(([key, value]: any) => (
                   <div key={key} className="flex items-center justify-between text-sm">
                     <span className="text-slate-300 capitalize">{key.replace('_', ' ')}</span>
                     <div className="flex items-center gap-3 w-1/2">
                        <div className="h-1.5 flex-1 bg-slate-700 rounded-full">
                           <div className="h-full bg-purple-500 rounded-full" style={{ width: `${Math.abs(value) * 100}%` }} />
                        </div>
                        <span className="text-xs font-mono text-slate-400">{Number(value).toFixed(2)}</span>
                     </div>
                   </div>
                 ))}
               </div>
            </div>
          )}

          <button className="w-full py-4 bg-white text-black hover:bg-slate-200 rounded-xl font-bold transition-all flex items-center justify-center gap-2">
            <Download size={20} /> Download Full Report
          </button>
        </div>
      </div>
    </div>
  )
}